.mythology
  position: relative
  z-index: 1
  padding: 82px 0 52px 0
  color: $color-white
  &__title
    font-weight: 700
    font-size: 48px
    line-height: 61px
    color: $color-white
    margin-bottom: 50px
  &__text
    font-size: 20px
    line-height: 28px
    text-align: center
    margin-bottom: 16px
    &:nth-of-type(2)
      margin-bottom: 70px
  &__img-cards
    display: flex
    justify-content: space-between

.card
  position: relative
  width: 420px
  height: 540px
  background: $color-card-bg
  border-radius: 4px
  padding: 40px
  box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.25)
  &__wrapper
    width: 100%
    height: 100%
    overflow: hidden
  &__title
    font-weight: 700
    font-size: 96px
    line-height: 122px
    margin-bottom: 20px
    color: $color-grey
  &__text
    font-size: 16px
    line-height: 26px
    color: $color-grey
  &__overlay
    width: 100%

    &:before
      content: ''
      position: absolute
      top: 0
      left: 0
      width: 50%
      height: 100%
      border-radius: 4px 0 0 4px
      transform-origin: bottom
      transition: .8s
    &_ra:before
      background: url('../img/ra.webp')
    &_anubis:before
      background: url('../img/anubis.webp')
    &_bast:before
      background: url('../img/bast.webp')

    &:after
      content: ''
      position: absolute
      top: 0
      right: 0
      width: 50%
      height: 100%
      border-radius: 0 4px 4px 0
      transform-origin: top
      transition: .8s

    &_ra:after
      background: url('../img/ra.webp')
      background-position: 210px
    &_anubis:after
      background: url('../img/anubis.webp')
      background-position: 210px
    &_bast:after
      background: url('../img/bast.webp')
      background-position: 210px

.card
  &:hover
    .card__overlay:before
      transform: rotateX(90deg) translateY(50%)
    .card__overlay:after
      transform: rotateX(90deg) translateY(-50%)
