.hero
  position: relative
  width: 100%
  height: 100vh
  @include section-bg($color-main-bg, '../img/hero-pyramids.webp')
  &__bottom
    position: absolute
    bottom: -2px
    left: 0
    right: 0
    width: 100%
    height: 250px
    transform-origin: bottom
  &__wrapper
    display: flex
    justify-content: center
    width: 100%

  &__title
    font-weight: 900
    font-size: 216px
    line-height: 281px
    margin-top: 136px
    width: 100%
    color: #fff
    text-align: center
    text-shadow: 0 5px 10px rgba(0, 0, 0, 1 )
    mix-blend-mode: overlay
    box-shadow: 0 5px 10px rgba(0, 0, 0, 1 )
    background: rgba(0, 0, 0, .5)
