@media screen and (max-width: 1520px)
  .container
    max-width: 1320px
  .hero
    &__title
      font-size: 192px
      line-height: 242px
      margin-top: 100px

@media screen and (max-width: 1440px)
  .container
    max-width: 1200px

  .card
    width: 390px
    height: 500px
    &__title
      font-weight: 700
      font-size: 80px
      line-height: 104px
      margin-bottom: 16px

    &__overlay
      &_ra:before
        background-size: cover
      &_anubis:before
        background-size: cover
      &_bast:before
        background-size: cover
      &_ra:after
        background-size: cover
        background-position: 195px
      &_anubis:after
        background-size: cover
        background-position: 195px
      &_bast:after
        background-size: cover
        background-position: 195px

@media screen and (max-width: 1240px)
  .container
    max-width: 1100px

  .card
    width: 350px
    height: 450px
    &__title
      font-size: 68px
      line-height: 72px
    &__overlay
      &_ra:after
        background-size: cover
        background-position: 175px
      &_anubis:after
        background-size: cover
        background-position: 175px
      &_bast:after
        background-size: cover
        background-position: 175px

@media screen and (max-width: 1140px)
  .container
    max-width: 992px

  .mythology
    &__img-cards
      flex-wrap: wrap
      justify-content: space-around
    &__card
      margin-bottom: 40px

@media screen and (max-width: 1024px)
  .container
    max-width: 960px

@media screen and (max-width: 992px)
  .container
    max-width: 750px

@media screen and (max-width: 768px)
  .container
    max-width: 96%

  .hero
    &__title
      font-size: 128px
      line-height: 184px
      margin-top: 96px

@media screen and (max-width: 472px)
  .hero
    &__wrapper
      align-items: center
      height: 100vh
    &__title
      font-size: 86px
      line-height: 136px
      margin-top: 0

  .mythology
    padding: 50px 0
    &__title
      text-align: center
      margin-bottom: 32px

  .card
    width: 300px
    height: 385px
    padding: 30px 20px

    &__title
      font-size: 58px
      line-height: 60px
    &__text
      line-height: 22px
    &__overlay
      &_ra:after
        background-size: cover
        background-position: 150px
      &_anubis:after
        background-size: cover
        background-position: 150px
      &_bast:after
        background-size: cover
        background-position: 150px
